import HighEnergy from '../misc/high-energy.csv';
import LowEnergy from '../misc/low-energy.csv';
import Random from '../misc/random.csv';
import MovingFast from '../misc/moving-fast-energy.csv';
import MovingSlow from '../misc/moving-slow-energy.csv';
import csv2array from './CSVToArray';

async function Main(csv) {

    // Initial Values
    let rawData;
    let time = [];
    let mpp = [];

    // Handles CSV Data
    async function getData() {
        if (csv === "High-Energy") {
            const response = await fetch(HighEnergy);
            const table = await response.text();
            rawData = csv2array(table);
        } else if (csv === "Low-Energy") {
            const response = await fetch(LowEnergy);
            const table = await response.text();
            rawData = csv2array(table);
        } else if (csv === "Random") {
            const response = await fetch(Random);
            const table = await response.text();
            rawData = csv2array(table);
        } else if (csv === "Moving-Fast") {
            const response = await fetch(MovingFast);
            const table = await response.text();
            rawData = csv2array(table);
        } else if (csv === "Moving-Slow") {
            const response = await fetch(MovingSlow);
            const table = await response.text();
            rawData = csv2array(table);
        }
    }

    const findMaxPowerPoint = (input) => {
        let i = 0;
        let temp_time = [];
        let voltage = [];
        let current = [];
        input.forEach((item, index, array) => {
            temp_time.push(item[0]);
            voltage.push(item[1]);
            current.push(item[2]);
            i = i + 1;
            if (i === 512) {
                time.push(Math.max.apply(Math, temp_time));
                mpp.push(Math.max.apply(Math, dotProduct(zipArray(voltage, current))));
                voltage = [];
                current = [];
                i = 0;
            }
        })
    }

    const zipArray = (array1, array2) => {
        const zipArray = array1.map(function (e, i) {
            return [e, array2[i]];
        });
        return zipArray;
    }

    const dotProduct = (array) => {
        const dotProduct = array.map((item) => {
            return [(item[0] * item[1])];
        })
        return dotProduct;
    }

    const formatData = (input) => {
        const data = []
        input.forEach((item, index, array) => {
            data.push({ Time: item[0], Power: item[1]*1000 })
        })
        return data
    }

    // Script
    await getData();
    findMaxPowerPoint(rawData);
    const data = zipArray(time, mpp);
    const formattedData = formatData(data);
    return formattedData;

}

export default Main
