import React, { PureComponent } from 'react';
import GraphScript from '../scripts/GraphScript'
import { ComposedChart, Area, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, Label } from 'recharts';

export default class Example extends PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            onTimeAccuracy: [],
            offTimeAccuracy: []
        };
        this.onTimeAccuracy = props.onTimeAccuracy;
        this.offTimeAccuracy = props.offTimeAccuracy;
        this.csv = props.csv;
    }

    componentDidMount() {
        GraphScript(this.csv).then((response) => { this.setState({ data: response }); console.log(response) });
        this.setState({ onTimeAccuracy: this.props.offTimeAccuracy });
        this.setState({ onTimeAccuracy: this.props.offTimeAccuracy });
    }

    componentDidUpdate(prevProps) {
        if (prevProps.csv !== this.props.csv) {
            GraphScript(this.props.csv).then((response) => { this.setState({ data: response }); console.log(response) });
        }
        if (prevProps.onTimeAccuracy !== this.props.onTimeAccuracy) {
            this.setState({ onTimeAccuracy: this.props.onTimeAccuracy });
        }
        if (prevProps.offTimeAccuracy !== this.props.offTimeAccuracy) {
            this.setState({ offTimeAccuracy: this.props.offTimeAccuracy });
        }
        console.log(this.state)
    }

    render() {
        return (
            <ResponsiveContainer width="100%" height="100%">
                <ComposedChart
                    width={500}
                    height={300}
                    margin={{
                        top: 5,
                        right: 0,
                        left: 0,
                        bottom: 10,
                    }}
                >
                    <defs>
                        <linearGradient id="colorPv" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#2AB06E" stopOpacity={0.8} />
                            <stop offset="95%" stopColor="#2AB06E" stopOpacity={0.4} />
                        </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="Time" type="number" domain={['dataMin', 'dataMax']} ticks={[15, 30, 45, 60]}>
                        <Label value="Time" offset={-5} position="insideBottom" />
                    </XAxis>
                    <YAxis>
                        <Label value="Power (mW)" offset={0} position="middle" angle={-90} />
                    </YAxis>
                    <YAxis yAxisId="right" orientation="right" domain={[0, 100]}>
                        <Label value="Accuracy (%)" offset={0} position="middle" angle={-90} />
                    </YAxis>
                    <Tooltip />
                    <Legend verticalAlign="top" height={36} />
                    <Area data={this.state.data} type="monotone" dataKey="Power" stroke="#2AB06E" fillOpacity={1} fill="url(#colorPv)" activeDot={{ r: 2 }} />
                    <Line yAxisId="right" data={this.state.onTimeAccuracy} type="monotone" dataKey="adaptedAccuracy" stroke="#E33737" strokeWidth={0.5} activeDot={{ r: 2 }} />
                    <Line yAxisId="right" data={this.state.offTimeAccuracy} type="monotone" dataKey="originalAccuracy" stroke="#5559D9" strokeWidth={0.5} activeDot={{ r: 2 }} />
                </ComposedChart>
            </ResponsiveContainer>
        );
    }
}
